import { evntBus } from "../../bus";
import Customer from "./Customer.vue";
export default {
  props: [
    // "items"
  ],
  data() {
    return {
      items_discount: 0,
      additional_discount: 0,
      total_tax: 0,
      total: 0,
      items: [],
    };
  },
  components: {
    Customer,
  },
  computed: {
    total_qty() {
      let qty = 0;
      this.items.forEach((item) => {
        qty += item.qty;
      });
      return qty;
    },
    subtotal() {
      let sum = 0;
      this.items.forEach((item) => {
        sum += item.qty * item.price;
      });
      return sum;
    },
  },
  methods: {
    // sortBy(prop) {
    //   this.projects.sort((a, b) => (a[prop] < b[prop] ? -1 : 1));
    // },
    remove_item(item) {
      const index = this.items.findIndex((el) => el === item);
      this.items.splice(index, 1);
    },
    add_one(item) {
      item.qty++;
      // this.$forceUpdate();
    },
    subtract_one(item) {
      item.qty--;
      if (item.qty <= 0) {
        this.remove_item(item);
      }
      // this.$forceUpdate();
    },
    add_item(item) {
      const index = this.items.findIndex((el) => el.name === item.name);
      if (index === -1) {
        const new_item = { ...item };
        new_item.qty = 1;
        new_item.vat = 18;
        new_item.price = 25;
        (new_item.active = false), this.items.unshift(new_item);
      } else {
        this.items[index].qty++;
      }
    },
  },
  created() {
    evntBus.$on("add_item", (item) => {
      this.add_item(item);
    });
  },
  mounted: function () {
